import Grid from "@material-ui/core/Grid";
import React, {useState} from "react";
import PlayCircleOutlineIcon from '@material-ui/icons/PlayCircleOutline';
import PauseCircleOutlineIcon from '@material-ui/icons/PauseCircleOutline';
import Slider from "@material-ui/core/Slider";
import IconButton from "@material-ui/core/IconButton";
import {makeStyles} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
    item: {
        marginRight: theme.spacing(2),
    },
}));

function formatTime(num) {
    let str = num + '';
    if (str.length < 2) return '0' + str;
    return str;
}

function frameToTime(totalFrames, currentFrame, framerate) {
    if (!framerate || framerate <= 0) return '00:00:00';
    let leftSeconds = Math.ceil((totalFrames - currentFrame) / framerate);
    let s = leftSeconds % 60;
    let leftMinute = (leftSeconds - s) / 60;
    let m = leftMinute % 60;
    let h = (leftMinute - m) / 60;
    return formatTime(h) + ':' + formatTime(m) + ':' + formatTime(s);
}

function reducer(state, action) {
    switch (action.type) {
        case 'play':
            return {...state, isPlay: true};
        case 'stop':
            return {...state, isPlay: false};
        default:
            throw new Error();
    }
}

function FrameController({autoplay, onPlay, onPause, totalFrames, currentFrame, framerate, onSlider,onSliderCommitted}, ref) {
    const classes = useStyles();
    //const [isPlay, setIsPlay] = useState(autoplay);
    const [state, dispatch] = React.useReducer(reducer, {isPlay: autoplay});
    React.useImperativeHandle(ref, () => ({
        play: () => {
            dispatch({type: 'play'});
        },
        pause: () => {
            dispatch({type: 'stop'});
        },
        isPlay: () => {
            return state.isPlay;
        },
    }));
    const handlePlay = () => {
        dispatch({type: 'play'});
        if (onPlay) onPlay();
    };
    const handlePause = () => {
        dispatch({type: 'stop'});
        if (onPause) onPause();
    };
    return <Grid
        container
        wrap="nowrap"
        alignItems="center"
    >
        {state.isPlay ?
            <IconButton className={classes.item} onClick={handlePause}><PauseCircleOutlineIcon/></IconButton> :
            <IconButton className={classes.item} onClick={handlePlay}><PlayCircleOutlineIcon/></IconButton>}
        <Slider className={classes.item} max={totalFrames} value={currentFrame} onChange={onSlider} onChangeCommitted={onSliderCommitted}/>
        <span className={classes.item}>-{frameToTime(totalFrames, currentFrame, framerate)}</span>
    </Grid>
}

FrameController = React.forwardRef(FrameController);
export default FrameController
