import React, {useState, useEffect} from "react";
import Grid from "@material-ui/core/Grid";
import {useTranslation} from '../components/language/translater';
import {useInterval} from "../components/useInterval";
import Header from "../components/common/Header";
import {makeStyles} from "@material-ui/core";
import Button from "@material-ui/core/Button";
import Link from "@material-ui/core/Link";
import {detectOS, downloads} from '../utils';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import TableContainer from "@material-ui/core/TableContainer";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import Container from "@material-ui/core/Container";
import axios from "axios";

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: '#fff',
        backgroundImage: 'url(/images/Pattern-Randomized.svg)'
    },
    bannerContainer: {
        backgroundColor: '#000',
        color: '#fff',
        paddingTop: '70px',
        paddingBottom: '70px',
    },
    bannerContainerSmall: {
        backgroundColor: '#000',
        color: '#fff',
        paddingBottom: '70px',
    },
    bannerWrap: {
        width: '66vw',
        height: '44vw',
        //marginLeft:'50px',
    },
    banner: {
        width: '100%',
        height: '100%',
        backgroundColor: '#fff',
    },
    downloadWrap: {
        flexGrow: 1,
        marginLeft: '50px',
        marginRight: '50px',
    },
    describe: {
        fontWeight: 300,
        lineHeight: 1.556,
        fontSize: '1.125rem',
    },
    bannerHolder: {
        fontWeight: 300,
        lineHeight: 1.556,
        fontSize: '1.5rem',
        color: '#fff',
    },
    next: {
        marginTop: 30,
        '&:last-child': {
            marginBottom: 30,
        }
    }
}));

const imgs = [
    'app_preview',
    'source_maker',
    'harvest',
];

const downloadLink = Object.fromEntries(Object.entries(downloads).map(([k, v]) => [k, v[0].src]));

export default function Home() {
    const big = useMediaQuery('(min-width:900px)');
    const {t} = useTranslation();
    const classes = useStyles();
    const [current, setCurrent] = useState(0);
    const [delay, setDelay] = useState(3000);
    const [img, setImg] = useState(t(`banners.${imgs[current]}.img`));
    const selectBanner = (index) => {
        index = index % imgs.length;
        setCurrent(index);
        setImg(t(`banners.${imgs[index]}.img`));
    };
    const [exchange, setExchange] = useState({CNY: 1, USD: 0.13});
    useEffect(() => {
        axios.get(`/api/currency/exchange`)
            .then(res => {
                if (0 === res.data.code) {
                    let e = {...exchange};
                    res.data.data.forEach(i => {
                        e[i.name] = parseFloat(i.exchange);
                    });
                    setExchange(e);
                }
            });
    }, []);
    useInterval(() => selectBanner(current + 1), delay);
    return <Grid
        container
        direction="column"
        className={classes.root}
    >
        <Header/>
        <Grid
            container
            direction={big ? "row" : "column"}
            wrap="nowrap"
            className={big ? classes.bannerContainer : classes.bannerContainerSmall}
        >
            {big ? null : <Grid
                className={classes.downloadWrap}
            >
                <h1>{process.env.REACT_APP_NAME}</h1>
                <Grid
                    container
                    wrap="nowrap"
                    alignItems="baseline"
                    justify="space-between"
                >
                    <p className={classes.describe}>{t('app.describe')}</p>
                    <Button variant="contained" color="primary">
                        {t('Download')} for {detectOS()}
                    </Button>
                </Grid>
            </Grid>}
            <Grid
                container
                direction="column"
                className={big ? classes.bannerWrap : null}
                justify="center"
                alignItems="center"
            >
                <img src={img} className={classes.banner} alt=""/>
            </Grid>
            {big ? <Grid
                className={classes.downloadWrap}
            >
                <h1>{process.env.REACT_APP_NAME}</h1>
                <p>&nbsp;</p>
                <p className={classes.describe}>{t('app.describe')}</p>
                <p>&nbsp;</p>
                <Button variant="contained" fullWidth color="primary" component="a" download
                        href={downloadLink[detectOS()][t('lang')]}>
                    {t('Download')} for {detectOS()}
                </Button>
                <ul>
                    {imgs.map((name, i) =>
                        <li key={i}><p key={i}><Link
                            className={classes.bannerHolder}
                            onMouseOver={() => {
                                setDelay(null);
                                selectBanner(i)
                            }}
                            onMouseLeave={() => {
                                setDelay(3000);
                            }}
                        >{t(`banners.${name}.alt`)}</Link></p></li>)}
                </ul>
            </Grid> : null}
        </Grid>

        <Container
            className={classes.next}
        >
            <TableContainer component={Paper}>
                <Table className={classes.table} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell> </TableCell>
                            <TableCell align="center">{t("Free")}</TableCell>
                            <TableCell align="center">{t("Member")}</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        <TableRow>
                            <TableCell>{t("Price")}</TableCell>
                            <TableCell align="center">0</TableCell>
                            <TableCell
                                align="center">{(15 * exchange[t('memberPriceUnitType')]).toFixed(2)}{t("_")}{t('memberPriceUnit')}</TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>{t("Device Limit")}</TableCell>
                            <TableCell align="center">1</TableCell>
                            <TableCell align="center">2</TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>{t("Source Limit")}</TableCell>
                            <TableCell align="center">15</TableCell>
                            <TableCell align="center">{t("No limit")}</TableCell>
                        </TableRow>
                        <TableRow>
                            <TableCell>{t("Free Powerful Plugins")}</TableCell>
                            <TableCell align="center">{t("Less")}</TableCell>
                            <TableCell align="center">{t("Most")}</TableCell>
                        </TableRow>
                    </TableBody>
                </Table>
            </TableContainer>
        </Container>
    </Grid>;
}
