import React, {useState, useEffect} from 'react';
import axios from 'axios';
import CircularProgress from "@material-ui/core/CircularProgress";
import Grid from "@material-ui/core/Grid";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import AppBar from "@material-ui/core/AppBar";
import {makeStyles} from "@material-ui/core/styles";
import LanguageSelector from "../components/language/selector";
import {Switch, Case, Default} from "../components/Switch";
import OrderDetailsPaypal from "../components/payments/Paypal";
import {STATUS} from "../constants/status";
import {OrderDetailsShowOnly} from "../components/orders/ShowOnly";
import OrderDetailsMetamask from "../components/payments/Metamask";
import OrderDetailsQrcodePersonal from "../components/payments/QrcodePersonal";
import NotSupportPayment from "../components/payments/NotSupportPayment";
import {useTranslation} from '../components/language/translater';

const CODE = require('../constants/code');

const searchParams = new URL(window.location.href).searchParams;
const name = searchParams.get('id');
const token = searchParams.get('token');

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    title: {
        flexGrow: 1,
    },
}));

function OrderNotFound() {
    return <div>404</div>;
}


function OrderDetails({data}) {
    console.log(data);
    if (data.status > STATUS.PAYABLE) return <OrderDetailsShowOnly data={data}/>;
    switch (data.payment) {
        case 'qrcodePersonal':
            return <OrderDetailsQrcodePersonal data={data}/>;
        case 'paypal':
            return <OrderDetailsPaypal data={data}/>;
        case 'metamask':
            return <OrderDetailsMetamask data={data}/>;
        case 'wechat':
        case 'alipay':
            return <OrderDetailsShowOnly data={data}/>;
    }
    return <NotSupportPayment/>
}

export default function Order(props) {
    //todo res.data.code, res.status 处理
    const [hasOrder, setHasOrder] = useState(!!(name && token));
    const [data, setData] = useState({});
    const [loading, setLoading] = useState(true);
    const [code, setCode] = useState(0);
    const [httpStatus, setHttpStatus] = useState(0);
    const classes = useStyles();
    const {t} = useTranslation();
    useEffect(() => {
        if (!hasOrder) return;
        new Promise(resolve => setTimeout(resolve, 1000))
            .then(() => axios.get(`/pay/order?id=` + encodeURIComponent(name), {
                headers: {'Authorization': 'Bearer ' + token}
            }))
            .then(res => {
                console.log(res);
                setCode(res.data.code);
                if (res.data.code === 0) {
                    setData(res.data.data);
                } else {
                    setHasOrder(false);
                }
                setLoading(false);
            })
            .catch(e => {
                if (e.response) {
                    setHttpStatus(e.response.status)
                }
                console.log(e)
                setHasOrder(false);
            });
    }, []);
    //console.log();
    return (<Grid
        container
        direction="column"
        className={classes.root}
    >
        <AppBar position="sticky">
            <Toolbar>
                <Typography variant="h6" className={classes.title}/>
                <LanguageSelector/>
            </Toolbar>
        </AppBar>
        <Grid
            container
            direction="column"
            justify="center"
            alignItems="center"
            className={classes.root}
        >
            <Switch condition={httpStatus}>
                <Case value={401}>
                    <div>Unauthorization</div>
                </Case>
                <Default>
                    <Switch condition={code}>
                        <Case value={CODE.PAYMENT_QRCODE_ORDER_PAGE_ALREADY_OPEN}>
                            <div>{t('qrcodePersonal.pageAlreadyOpen')}</div>
                        </Case>
                        <Case value={CODE.PAYMENT_NOT_SUPPORT} component={<NotSupportPayment/>}/>
                        <Case value={CODE.ORDER_NOT_FOUND} component={<OrderNotFound/>}/>
                        <Default>
                            <Switch condition={hasOrder}>
                                <Case value={true}>
                                    <Switch condition={loading}>
                                        <Case value={true} component={<CircularProgress/>}/>
                                        <Case value={false} component={<OrderDetails data={data}/>}/>
                                    </Switch>
                                </Case>
                                <Case value={false} component={<OrderNotFound/>}/>
                            </Switch>
                        </Default>
                    </Switch>
                </Default>
            </Switch>
        </Grid>
    </Grid>);
}
