import {BigNumber} from "bignumber.js";
import {ethers} from "ethers";

const abis = require('./abi');
const erc20 = {
    'USDT': {ENS: 'TetherToken',address:process.env.REACT_APP_ETH_CONTRACT_USDT_ADDRESS, decimals: '1' + '0'.repeat(6)}
};
let supportCurrencyTranscate = {
    'ETH': {transaction: ethTransaction, balance: ethBalance, checkTransaction: ethCheckTransaction},
};

Object.entries(erc20).forEach(([name, item]) => {
    erc20[name].contract = new ethers.Contract(erc20[name].address, abis[erc20[name].ENS]);
    erc20[name].inter = new ethers.utils.Interface(abis[erc20[name].ENS]);
    supportCurrencyTranscate[name] = {
        transaction: erc20Transaction(name),
        balance: erc20Balance(name),
        checkTransaction: erc20CheckTransaction(name),
    }
});

function ethTransaction(signer, from, to, amount) {
    return signer.sendTransaction({
        from,
        to,
        value: ethers.utils.parseEther(amount)
    });
}

async function ethBalance(provider, account) {
    return ethers.utils.formatEther(await provider.getBalance(account));
}

async function ethCheckTransaction(provider, hash, from, to, amount) {
    const tx = await provider.getTransaction(hash);
    return tx &&
        tx.from.toLowerCase() === from.toLowerCase() &&
        tx.to.toLowerCase() === to.toLowerCase() &&
        ethers.BigNumber.from(tx.value).eq(ethers.utils.parseEther(amount));
}

function erc20Transaction(name) {
    return function (signer, from, to, amount) {
        const contract = erc20[name].contract.connect(signer);
        return contract.transfer(
            to,
            ethers.BigNumber.from(BigNumber(amount).multipliedBy(BigNumber(erc20[name].decimals)).toFixed(0).toString())
        );
    }
}

function erc20Balance(name) {
    return async function (provider, account) {
        const contract = erc20[name].contract.connect(provider);
        let balance = await contract.balanceOf(account);
        return BigNumber(ethers.BigNumber.from(balance).toString()).dividedBy(BigNumber(erc20[name].decimals)).toString();
    }
}

function erc20CheckTransaction(name) {
    return async function (provider, hash, from, to, amount) {
        const inter = erc20[name].inter;
        const tx = await provider.getTransaction(hash);
        if (!tx || tx.from.toLowerCase() !== from.toLowerCase()) return false;
        const decodedInput = inter.parseTransaction({data: tx.data, value: tx.value});
        return 'transfer' === decodedInput.name && decodedInput.args[0].toLowerCase() === to.toLowerCase() &&
            ethers.BigNumber.from(decodedInput.args[1]).eq(
                ethers.BigNumber.from(BigNumber(amount).multipliedBy(BigNumber(erc20[name].decimals)).toFixed(0).toString())
            );
    }
}

export {supportCurrencyTranscate}
