import React, {useState} from "react";
import MenuItem from "@material-ui/core/MenuItem";
import LanguageIcon from '@material-ui/icons/Translate';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/core";
import Tooltip from "@material-ui/core/Tooltip";
import Button from "@material-ui/core/Button";
import NoSsr from "@material-ui/core/NoSsr";
import Menu from "@material-ui/core/Menu";
import {SUPPORT_LANGUAGES, i18next, i18nPromise} from './translater';

const useStyles = makeStyles((theme) => ({
    root: {
        width: "auto",
    },
}));

export default function Selector() {
    const classes = useStyles();
    const [userLanguage, setUserLanguage] = useState(i18next.language);
    i18nPromise.then(() => setUserLanguage(i18next.language));
    const [languageMenu, setLanguageMenu] = useState(null);
    const handleLanguageIconClick = (event) => {
        setLanguageMenu(event.currentTarget);
    };
    const handleLanguageMenuClose = (event) => {
        if (event.currentTarget.nodeName === 'A') {
            localStorage.setItem('lang', event.currentTarget.lang);
            i18next.changeLanguage(event.currentTarget.lang);
        }
        setLanguageMenu(null);
    };
    return <Grid
        container
        direction="row"
        justify="center"
        alignItems="center"
        className={classes.root}
    >
        <Tooltip title="" enterDelay={300}>
            <Button
                color="inherit"
                aria-owns={languageMenu ? 'language-menu' : undefined}
                aria-haspopup="true"
                onClick={handleLanguageIconClick}
                data-ga-event-category="header"
                data-ga-event-action="language"
            >
                <LanguageIcon/>
                <span className={classes.language}>{SUPPORT_LANGUAGES[userLanguage]}</span>
                <ExpandMoreIcon fontSize="small"/>
            </Button>
        </Tooltip>
        <NoSsr defer>
            <Menu
                id="language-menu"
                anchorEl={languageMenu}
                open={Boolean(languageMenu)}
                onClose={handleLanguageMenuClose}
            >
                {Object.entries(SUPPORT_LANGUAGES).map(([code, text]) => (
                    <MenuItem
                        component="a"
                        data-no-link="true"
                        key={code}
                        selected={userLanguage === code}
                        onClick={handleLanguageMenuClose}
                        lang={code}
                        hrefLang={code}
                    >
                        {text}
                    </MenuItem>
                ))}
            </Menu>
        </NoSsr>
    </Grid>
}
