import Grid from "@material-ui/core/Grid";
import AdobeAnimate from "./AdobeAnimate";
import React from "react";
import FrameController from "./FrameController";
import {makeStyles} from "@material-ui/core";
import {useInterval} from "./useInterval";

const useStyles = makeStyles((theme) => ({
    root: {
        border: '1px solid #ccc',
    },
    item: {
        width: '100%',
    },
}));

export default function AdobeAnimateWithPlayControl({composition}) {
    const classes = useStyles();
    const aniRef = React.useRef(null);
    const fcRef = React.useRef(null);
    const [framerate, setFramerate] = React.useState(0);
    const [totalFrames, setTotalFrames] = React.useState(0);
    const [currentFrame, setCurrentFrame] = React.useState(0);
    const [delay, setDelay] = React.useState(2000);
    const handleTick = (evt) => {
        setCurrentFrame(evt.currentTarget.currentFrame);
        if (evt.currentTarget.currentFrame >= evt.currentTarget.totalFrames) {
            if (aniRef.current) aniRef.current.stop(0);
            if (fcRef.current) fcRef.current.pause();
        }
    };
    const handlePause = () => {
        if (aniRef.current) {
            aniRef.current.stop();
            aniRef.current.removeAllEventListeners();
        }
    };
    const handlePlay = () => {
        if (aniRef.current) {
            aniRef.current.play();
            aniRef.current.addEventListener(handleTick);
        }
    };
    const handleSlider = (event, newValue) => {
        aniRef.current.removeAllEventListeners();
        setCurrentFrame(newValue);
    };
    const handleSliderCommitted = (event, newValue) => {
        if (aniRef.current && fcRef.current) {
            if (fcRef.current.isPlay()) {
                aniRef.current.play(newValue);
                aniRef.current.addEventListener(handleTick);
            }
            else {
                aniRef.current.stop(newValue);
                aniRef.current.removeAllEventListeners();
            }
        }
    };
    useInterval(() => {
        if (aniRef.current && aniRef.current.isReady()) {
            setFramerate(aniRef.current.framerate());
            setTotalFrames(aniRef.current.totalFrames());
            setDelay(null);
        }
    }, delay);
    return <Grid
        className={classes.root}
    >
        <AdobeAnimate ref={aniRef} composition={composition} className={classes.item}/>
        <FrameController ref={fcRef} autoplay={false} currentFrame={currentFrame} framerate={framerate}
                         onPause={handlePause} onPlay={handlePlay}
                         onSlider={handleSlider}
                         onSliderCommitted={handleSliderCommitted}
                         totalFrames={totalFrames}/>
    </Grid>
}
