import './App.css';
import {
    BrowserRouter as Router,
    Switch,
    Route,
} from "react-router-dom";
import Order from './pages/order';
import React from "react";
import Qrcode from "./pages/payment_qrcode";
import Home from "./pages/home";
import Download from "./pages/download";
import About from "./pages/about";
import Guide from "./pages/guide";

function App() {
    return (<Router>
            <Switch>
                <Route path="/order" component={Order}/>
                <Route path="/pay/qrcode-personal/qrcode" component={Qrcode}/>
                {/*<Route path="/pay/qrcode-personal/expired" component={Qrcode}/>
                <Route path="/pay/qrcode-personal/tips" component={Qrcode}/>
                <Route path="/pay/error" component={Qrcode}/>*/}
                <Route path="/download" component={Download}/>
                <Route path="/guide" component={Guide}/>
                <Route path="/about" component={About}/>
                <Route path="/" component={Home}/>
            </Switch>
        </Router>);
}


export default App;
