import {useEffect, useState} from "react";
import {useTranslation} from '../language/translater';
import {useForm} from "react-hook-form";
import React from "react";
import Backdrop from "@material-ui/core/Backdrop/Backdrop";
import {BigNumber} from "bignumber.js";
import Modal from "@material-ui/core/Modal";
import FormControl from "@material-ui/core/FormControl";
import FormInput from "../form/FormInput";
import IconButton from "@material-ui/core/IconButton";
import Button from "@material-ui/core/Button";
import HelpIcon from '@material-ui/icons/Help';
import {makeStyles} from "@material-ui/core";
import {useSnackbar} from "notistack";
import {getDefault, getOrderTradeIdLocalStorage, setOrderTradeIdLocalStorage} from '../../utils'
import Grid from "@material-ui/core/Grid";
import axios from "axios";
import Container from "@material-ui/core/Container";

const WEBSOCKET_CODE = require('../../constants/wscode');
const QRCode = require('qrcode.react');

const searchParams = new URL(window.location.href).searchParams;
const name = searchParams.get('id');
const token = searchParams.get('token');

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        transition: 'background-color 0.5s linear',
    },
    rootWechat: {
        flexGrow: 1,
        backgroundColor: '#1ea939',
        transition: 'background-color 0.5s linear',
    },
    rootAlipay: {
        flexGrow: 1,
        backgroundColor: '#00a1e9',
        transition: 'background-color 0.5s linear',
    },
    container: {
        flexGrow: 1,
        backgroundColor: '#fff',
        width: '400px',
    },
    payment: {
        width: '150px',
        padding: '0 8px'
    },
    modal: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
    },
    paper: {
        backgroundColor: theme.palette.background.paper,
        border: '2px solid #000',
        boxShadow: theme.shadows[5],
        padding: theme.spacing(2, 4, 3),
        outline: 0,
        maxHeight: '80vh',
        maxWidth: '80vw',
        overflow: 'auto',
    },
    mask: {
        width: '300px',
        height: '300px',
        position: 'absolute',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        backgroundColor: 'rgba(255,255,255,0.6)',
        padding: '15px',
        boxSizing: 'border-box',
    },
    blur: {
        filter: 'blur(5px)',
    },
    p: {
        marginBlockStart: '3px',
        marginBlockEnd: '3px',
        wordBreak: 'break-all',
    }
}));

const supports = {
    'wechat': {
        'translate': {
            'tradeId': 'qrcodePersonal.wechatTradeId',
            'tradeIdTips': 'qrcodePersonal.wechatTradeIdTips',
        },
        'root': 'rootWechat',
        'help': [1, 2, 3, 4, 5],
    },
    'alipay': {
        'translate': {
            'tradeId': 'qrcodePersonal.alipayTradeId',
            'tradeIdTips': 'qrcodePersonal.alipayTradeIdTips',
        },
        'root': 'rootAlipay',
        'help': [1, 2, 3, 4],
    },
};

export default function OrderDetailsQrcodePersonal({data}) {
    const [tradeId, setTradeId] = useState(getOrderTradeIdLocalStorage(name, ''));
    const [scan, setScan] = useState(0);
    const [payment, setPayment] = useState('');
    const [wsMessage, setWsmessage] = useState('');
    const [inputTradeId, setInputTradeId] = useState('');
    const classes = useStyles();
    const {t, i18n} = useTranslation();
    let u = new URL(data.tradeUrl);
    const sessionId = u.searchParams.get('id');
    u.protocol = u.protocol.indexOf('https') >= 0 ? 'wss:' : 'ws:';
    u.pathname = '/pay/qrcode-personal/session';
    u.search = '?token=' + encodeURIComponent(token) + '&id=' + encodeURIComponent(name);
    const {enqueueSnackbar} = useSnackbar();
    const {handleSubmit, reset, watch, control, formState: {errors}} = useForm();
    const watchTradeId = watch('tradeId');
    const [finished, setFinished] = useState(false);
    const [openConfirm, setOpenConfirm] = React.useState(false);
    const handleUpload = () => {
        if (!inputTradeId) return enqueueSnackbar(t('tips.tradeIdInvalid'), {variant: 'error'});
        setTradeId(inputTradeId);
        setOrderTradeIdLocalStorage(name, inputTradeId);
        axios.post('/pay/changeOrderToPending', {
            id: name,
            tradeId: inputTradeId,
            sessionId,
            lang: localStorage.getItem('lang')
        }, {
            headers: {'Authorization': 'Bearer ' + token}
        })
            .then((res) => {
                if (res.data.code) return enqueueSnackbar(t('tips.uploadTransactionIdFailed'), {variant: 'error'});
                setFinished(true);
                enqueueSnackbar(t('tips.checkoutSuccess'), {variant: 'success'});
                setOpenConfirm(false);
                setTimeout(() => window.location.reload(), 3000);
            })
            .catch(e => {
                if (e.response && 401 === e.response.status) return enqueueSnackbar(t('tips.uploadTransactionIdFailedCauseAuth'), {variant: 'error'});
                enqueueSnackbar(t('tips.uploadTransactionIdFailed'), {variant: 'error'});
                console.error(e)
            })
    };
    const onConfirm = data => {
        setInputTradeId(data.tradeId);//防止意外修改了input
        setOpenConfirm(true);
    };
    const onConfirmCancel = () => setOpenConfirm(false);

    const [openHelp, setOpenHelp] = React.useState(false);
    const handleOpenHelp = () => setOpenHelp(true);
    const handleCloseHelp = () => setOpenHelp(false);

    useEffect(() => {
        let count = 0, tmpPay = '';
        const ws = new WebSocket(u.toString());
        ws.addEventListener('error', function (e) {
            console.log(e)
        });
        ws.addEventListener('close', function (e) {
            switch (e.code) {
                case WEBSOCKET_CODE.UNAUTHORIZATION:
                    setWsmessage('tips.notAuth');
                    break;
                case WEBSOCKET_CODE.ORDER_NOT_FOUND:
                    setWsmessage('tips.orderNotFound');
                    break;
                case WEBSOCKET_CODE.PAYMENT_QRCODE_ORDER_PAGE_ALREADY_OPEN:
                    setWsmessage('qrcodePersonal.pageAlreadyOpen');
                    break;
                case WEBSOCKET_CODE.PAYMENT_QRCODE_PERSONAL_SESSION_EXPIRED:
                    setWsmessage('qrcodePersonal.sessionExpired');
                    break;
                case WEBSOCKET_CODE.PAYMENT_QRCODE_SUCCESS:
                    setWsmessage('tips.checkoutSuccess');
                    break;
                case WEBSOCKET_CODE.UNKNOWN_ERROR:
                default:
                    setWsmessage('tips.unknownError');
                    break;
            }
            console.log(e)
        });
        ws.addEventListener('message', function incoming(event) {
            count++;
            const steps = JSON.parse(event.data);
            setScan(steps.scan);
            setPayment(steps.payment);
            if (count > 1 && tmpPay !== steps.payment) reset();
            tmpPay = steps.payment;
        });
    }, []);
    return <Grid
        container
        direction="column"
        justify="center"
        alignItems="center"
        className={classes[getDefault.bind(supports[payment])('root', 'root')]}
    >
        <Modal
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            className={classes.modal}
            open={openHelp}
            onClose={handleCloseHelp}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
                timeout: 500,
            }}
        >
            <Container maxWidth="sm" className={classes.paper}>
                <h2>{t('Help')}</h2>
                <p>{t(getDefault.bind(supports[payment])('translate.tradeIdTips', ''))}</p>
                <Grid container>
                    {getDefault.bind(supports[payment])('help', [])
                        .map(n => <img key={n} src={`/images/help/${payment}_help_${n}.png`} alt=""/>)}
                </Grid>
            </Container>
        </Modal>
        <Modal
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            className={classes.modal}
            open={openConfirm}
            onClose={onConfirmCancel}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
                timeout: 500,
            }}
        >
            <Container maxWidth="sm" className={classes.paper}>
                <h2>{t('qrcodePersonal.confirmTips.0')}</h2>
                <Grid container>
                    <p className={classes.p}>{t('Transaction Id') + t('：') + inputTradeId}</p>
                    <p className={classes.p}>{t('qrcodePersonal.confirmTips.1')}</p>
                    <Button className={classes.root} fullWidth color="primary" variant="contained"
                            onClick={handleUpload}>{t('Confirm')}</Button>
                    <Button className={classes.root} fullWidth variant="contained"
                            onClick={onConfirmCancel}>{t('Cancel')}</Button>
                </Grid>
            </Container>
        </Modal>
        <Grid
            container
            direction="column"
            justify="center"
            alignItems="center"
            className={classes.container}
        >
            <Grid
                container
                justify="center"
                alignItems="center"
                style={{paddingBottom: '15px'}}
            >
                <img className={classes.payment}
                     src="/images/Alipay_logo.svg"
                     alt=""/>
                <img className={classes.payment}
                     src="/images/WeChat_Pay.png"
                     alt=""/>
            </Grid>
            <Grid
                container
                justify="center"
                alignItems="center"
            >
                <QRCode className={wsMessage ? classes.blur : {}} value={data.tradeUrl + '&lang=' + i18n.language}
                        size={300}/>
                {wsMessage ? <div className={classes.mask}><b>{t(wsMessage)}</b></div> : null}
            </Grid>
            <p className={classes.p}>{t('Order Id')}{t('：')}{data.name}</p>
            {finished ? <p className={classes.p}>{t('Transaction Id') + t('：') + tradeId}</p> : null}
            <p className={classes.p}>{t('Total')}({t(data.currencyName)}){t('：')}{BigNumber(data.total).toFixed(2)}</p>
            {BigNumber(data.platformFee).isGreaterThan(0) ?
                <>
                    <p className={classes.p}>{t('Platform Fee')}({t(data.currencyName)}){t('：')}{BigNumber(data.platformFee).toFixed(2)}</p>
                    <p className={classes.p}>{t('tips.platformFee')}</p>
                </>
                : null}
            {payment ? null : <p className={classes.p}>{t('qrcodePersonal.scanQrcodeToPay.0')}</p>}
            {payment ? null : <p className={classes.p}>{t('qrcodePersonal.scanQrcodeToPay.1')}</p>}

            {finished ? <Button variant="contained" disabled>{t('Success')}</Button> : null}
            {!finished && scan && payment ? <>
                <p className={classes.p}>{t('Payment')}{t('：')}<b>{t(`pay.${payment}`)}</b></p>
                <form noValidate autoComplete="off" onSubmit={handleSubmit(onConfirm)}>
                    <FormControl required style={{width: '300px'}}>
                        <FormInput
                            label={t('Transaction Id') + '(' + t(getDefault.bind(supports[payment])('translate.tradeId', '')) + ')'}
                            control={control} name={"tradeId"}
                            rules={{required: true, pattern: /^[0-9]{28,}$/}}
                            defaultValue={tradeId}
                            helperText={t('qrcodePersonal.tradeIdInputHelperText') + ' (' + (watchTradeId ? watchTradeId.length : 0) + ')'}/>
                        <Grid
                            container
                            direction="row"
                            alignItems="center"
                            wrap="nowrap"
                        >
                            <span
                                className="MuiFormHelperText-root">{t(getDefault.bind(supports[payment])('translate.tradeIdTips', ''))}</span>
                            <IconButton aria-label="help" type="button" onClick={handleOpenHelp}>
                                <HelpIcon/>
                            </IconButton>
                        </Grid>
                        <Button variant="contained" type="submit">{t('Submit')}</Button>
                    </FormControl>
                </form>
            </> : null}
        </Grid>
    </Grid>
}
