function getOrderTradeIdLocalStorageKey(name) {
    return `order:${name}`
}

function getOrderTradeIdLocalStorage(name, defaultValue) {
    return window.localStorage.getItem(getOrderTradeIdLocalStorageKey(name)) || defaultValue;
}

function setOrderTradeIdLocalStorage(name, tradeId) {
    if ("undefined" === typeof tradeId) return;//不保存未定义的变量
    return window.localStorage.setItem(getOrderTradeIdLocalStorageKey(name), tradeId);
}

/**
 * usage: getDefault.bind(global)(name, defaultValue)
 * @param {string} name
 * @param {*} [defaultValue]
 * @returns {*}
 */
function getDefault(name, defaultValue) {
    let keys = name.split('.');
    let object = this;
    if ('object' !== typeof object) return defaultValue;
    for (let i = 0; i < keys.length; i++) {
        object = object[keys[i]];
        if (!object) return defaultValue;
    }
    return object;
}

