const detectOSRegExp = [
    {rule: /linux/, name: 'Linux'},
    {rule: /mac/, name: 'Mac'},
];

function detectOS() {
    const ua = window.navigator.userAgent.toLowerCase();
    for (let i = 0; i < detectOSRegExp.length; i++) {
        if (ua.match(detectOSRegExp[i].rule)) return detectOSRegExp[i].name;
    }
    return 'Windows';
}

function promiseHandles() {
    let resolve = null, reject = null;
    const promise = new Promise((_resolve, _reject) => {
        resolve = _resolve;
        reject = _reject;
    });
    return {promise, resolve, reject};
}

