import {useEffect, useState} from "react";
import {getOrderTradeIdLocalStorage, setOrderTradeIdLocalStorage} from "../../utils";
import {useSnackbar} from "notistack";
import detectEthereumProvider from "@metamask/detect-provider";
import {useTranslation} from '../language/translater';
import axios from "axios";
import {ethers} from "ethers";
import {supportCurrencyTranscate} from "../../constants/contracts";
import {BigNumber} from "bignumber.js";
import {Case, Switch} from "../Switch";
import React from "react";
import Grid from "@material-ui/core/Grid";
import {OrderDetailsShowOnlyTable} from "../orders/ShowOnly";
import CircularProgress from "@material-ui/core/CircularProgress";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import {makeStyles} from "@material-ui/core";
import Container from "@material-ui/core/Container";

const searchParams = new URL(window.location.href).searchParams;
const name = searchParams.get('id');
const token = searchParams.get('token');

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    breakWord: {
        wordBreak: 'break-all',
    },
    img: {
        filter: 'grayscale(1)',
        width: '80%',
    },
}));

export default function OrderDetailsMetamask({data}) {
    const [tradeId, setTradeId] = useState(getOrderTradeIdLocalStorage(name, ''));
    const [hasExt, setHasExt] = useState(!!window.ethereum);
    const [paying, setPaying] = useState(false);
    const [finished, setFinished] = useState(false);
    const {enqueueSnackbar} = useSnackbar();
    useEffect(() => {
        setTimeout(async () => {
            const provider = await detectEthereumProvider();
            setHasExt(!!(provider && provider === window.ethereum));
        });
    }, []);
    const classes = useStyles();
    const {t} = useTranslation();
    const handleUpload = () => {
        axios.post('/pay/changeOrderToPending', {id: name, tradeId, lang: localStorage.getItem('lang')}, {
            headers: {'Authorization': 'Bearer ' + token}
        })
            .then((res) => {
                if (res.data.code) return enqueueSnackbar(t('tips.uploadTransactionIdFailed'), {variant: 'error'});
                setFinished(true);
                enqueueSnackbar(t('tips.checkoutSuccess'), {variant: 'success'});
            })
            .catch(e => {
                if (e.response && 401 === e.response.status) return enqueueSnackbar(t('tips.uploadTransactionIdFailedCauseAuth'), {variant: 'error'});
                enqueueSnackbar(t('tips.uploadTransactionIdFailed'), {variant: 'error'});
                console.error(e)
            })
            .finally(() => setPaying(false));
        setPaying(true);
    };
    const handlePayment = () => {
        try {
            setPaying(true);
            window.ethereum.request({method: 'eth_requestAccounts'})
                .then(async (accounts) => {
                    if (accounts.length <= 0) return enqueueSnackbar(t('metamask.needAuthWithMetamask'), {variant: 'info'});
                    const provider = new ethers.providers.Web3Provider(window.ethereum);
                    const signer = provider.getSigner();
                    const network = await provider.getNetwork();
                    console.log(network);
                    if (!network || process.env.REACT_APP_NETWORK_NAME !== network.name ||
                        process.env.REACT_APP_NETWORK_CHAIN_ID !== "" + network.chainId)
                        return enqueueSnackbar(t('tips.metamaskNetworkTypeInvalid'), {variant: 'info'});
                    const tool = supportCurrencyTranscate[data.currencyName];
                    //todo 思考是否有必要验证from
                    if (tradeId && await tool.checkTransaction(provider, tradeId, accounts[0], process.env.REACT_APP_ETH_ACCOUNT_ADDRESS, data.total)) {//检测是否有该交易，防止进行多次交易
                        setPaying(false);
                        return enqueueSnackbar(t('tips.checkPayAlready'), {variant: 'info'});
                    }
                    if (!tool) return enqueueSnackbar(t('tips.currencyNotSupport'), {variant: 'info'});
                    let balance = await tool.balance(provider, accounts[0]);
                    console.log('balance', balance);
                    if (BigNumber(balance).isLessThan(BigNumber(data.total))) return enqueueSnackbar(t('tips.balanceNotEnough'), {variant: 'info'});
                    //todo 检测gas是否足够？
                    const res = await tool.transaction(signer, accounts[0], process.env.REACT_APP_ETH_ACCOUNT_ADDRESS, data.total).catch(err => {
                        enqueueSnackbar(t('tips.transactionFailed'), {variant: 'error'});
                        setPaying(false);
                    });
                    setTradeId(res.hash);
                    if (res.hash) setOrderTradeIdLocalStorage(name, res.hash);
                    handleUpload();
                })
                .catch(err => {
                    if (err.code === 4001) {
                        // EIP-1193 userRejectedRequest error
                        // If this happens, the user rejected the connection request.
                        enqueueSnackbar(t('metamask.needAuthWithMetamask'), {variant: 'info'});
                    } else {
                        console.error(err);
                    }
                })
                .finally(() => setPaying(false));
        } catch (e) {
            enqueueSnackbar(t('tips.unknownErrorOnPay'), {variant: 'error'});
            console.error(e)
        }
    };
    const handleCopy = () => {
        let aux = document.createElement("input");
        aux.setAttribute("value", window.location.href);
        document.body.appendChild(aux);
        aux.select();
        document.execCommand("copy");
        document.body.removeChild(aux);
    };
    const handleRefresh = () => {
        window.location.reload();
    };
    return <Container maxWidth="sm">
        <Switch condition={hasExt}>
            <Case value={false}>
                <Grid
                    container
                    justify="center"
                    alignItems="center"
                    direction="column"
                >
                    <img src="/images/metamask-fox.svg" className={classes.img} alt="" draggable="false"/>
                    <p>
                        {t('metamask.installTips.0')}
                        <button onClick={handleCopy}>{t('metamask.installTips.1')}</button>
                        {t('metamask.installTips.2')}
                        <button onClick={handleRefresh}>{t('metamask.installTips.3')}</button>
                        {t('metamask.installTips.4')}
                    </p>
                    <Button fullWidth={true} variant="contained" href="https://metamask.io/download.html"
                            target="_blank">{t('Download')}</Button>
                </Grid>
            </Case>
            <Case value={true}>

                <OrderDetailsShowOnlyTable toFixed={15} data={data}/>
                {tradeId ? <p className={classes.breakWord}>{t('Transaction Id') + t('：') + tradeId}</p> : null}
                {paying ? <Grid
                    container
                    justify="center"
                    alignItems="center"
                    wrap="nowrap"
                ><CircularProgress/><span>{t('tips.onPay')}</span></Grid> : null}
                <Grid
                    container
                    justify="center"
                    alignItems="center"
                    wrap="nowrap"
                >
                    <Avatar src="/images/metamask-fox.svg" alt="" draggable="false"/>
                    {finished ?
                        <Button className={classes.root} variant="contained" disabled>{t('Success')}</Button> :
                        <Button className={classes.root} variant="contained"
                                onClick={handlePayment} disabled={paying}>{t('Checkout')}</Button>}
                    {tradeId ? <Button variant="contained" color="primary"
                                       onClick={handleUpload}>{t('Resubmit')}</Button> : null}
                </Grid>
                {tradeId ? <p>{t('tips.whatIsResubmit')}</p> : null}
            </Case>
        </Switch>
    </Container>;
}
