import React from "react";
import AdobeAn from '../flash';
import {promiseHandles} from "../utils";
import {useTranslation} from '../components/language/translater';

function init(composition,) {
    const {promise, resolve, reject} = promiseHandles();
    let comp = AdobeAn.getComposition(composition);
    let lib = comp.getLibrary();
    let loader = new window.createjs.LoadQueue(false);
    loader.addEventListener("fileload", function (evt) {
        let images = comp.getImages();
        if (evt && (evt.item.type === "image")) {
            images[evt.item.id] = evt.result;
        }
    });
    loader.addEventListener("complete", function (evt) {
        //This function is always called, irrespective of the content. You can use the variable "stage" after it is created in token create_stage.
        let lib = comp.getLibrary();
        let ss = comp.getSpriteSheet();
        let queue = evt.target;
        let ssMetadata = lib.ssMetadata;
        for (let i = 0; i < ssMetadata.length; i++) {
            ss[ssMetadata[i].name] = new window.createjs.SpriteSheet({
                "images": [queue.getResult(ssMetadata[i].name)],
                "frames": ssMetadata[i].frames
            })
        }
        resolve(lib);
    });
    loader.addEventListener("error", function (evt) {
        reject(evt);
    });
    loader.loadManifest(lib.properties.manifest);
    return promise;
}

function reducer(state, action) {
    switch (action.type) {
        case 'framerate':
            return {...state, framerate: action.value};
        case 'isReady':
            return {...state, isReady: action.value};
        default:
            throw new Error();
    }
}

function AdobeAnimate({composition, className, mode, startPosition, loop, reversed, autoplay}, ref) {
    const {t} = useTranslation();
    const anchorRef = React.useRef(null);
    const exportRoot = React.useRef(null);
    const stage = React.useRef(null);
    const [state, dispatch] = React.useReducer(reducer, {framerate: null, isReady: false});
    React.useImperativeHandle(ref, () => ({
        isReady: () => {
            return state.isReady;
        },
        play: (frame) => {
            if (!exportRoot.current) return;
            if ('number' === typeof frame && frame >= 0) exportRoot.current.gotoAndPlay(frame);
            else exportRoot.current.play();
            window.createjs.Ticker.addEventListener("tick", stage.current);
        },
        stop: (frame) => {
            if (!exportRoot.current) return;
            if ('number' === typeof frame && frame >= 0) exportRoot.current.gotoAndStop(frame);
            else exportRoot.current.stop();
            window.createjs.Ticker.removeEventListener("tick", stage.current);
        },
        currentFrame: () => {
            if (!exportRoot.current) return 0;
            return exportRoot.current.currentFrame;
        },
        framerate: () => {
            return state.framerate;
        },
        totalFrames: () => {
            if (!exportRoot.current) return 0;
            return exportRoot.current.totalFrames;
        },
        addEventListener(listener) {
            if (!exportRoot.current) return false;
            exportRoot.current.addEventListener('tick', listener);
            return true;
        },
        removeEventListener(listener) {
            if (!exportRoot.current) return false;
            exportRoot.current.removeEventListener('tick', listener);
            return true;
        },
        removeAllEventListeners() {
            if (!exportRoot.current) return false;
            exportRoot.current.removeAllEventListeners('tick');
            return true;
        },
    }));
    React.useEffect(() => {
        init(composition).then((lib) => {
            dispatch({type: 'framerate', value: lib.properties.fps});
            anchorRef.current.width = lib.properties.width;
            anchorRef.current.height = lib.properties.height;
            exportRoot.current = lib.getExportRoot(mode, startPosition, loop, reversed);
            autoplay ? exportRoot.current.play(0) : exportRoot.current.stop(0);
            stage.current = new lib.Stage(anchorRef.current);
            //Code to support hidpi screens and responsive scaling.
            AdobeAn.compositionLoaded(lib.properties.id);
            //Registers the "tick" event listener.
            stage.current.addChild(exportRoot.current);
            window.createjs.Ticker.framerate = lib.properties.fps;//是不是意味着只能有一种帧率？
            window.createjs.Ticker.addEventListener("tick", stage.current);//todo 优化，不显示的时候。
            setTimeout(() => {
                if (!autoplay) window.createjs.Ticker.removeEventListener("tick", stage.current);
            }, 1500 / lib.properties.fps);//显示一帧
            dispatch({type: 'isReady', value: true});
        }).catch(e => console.error(e));
        return () => {
            window.createjs.Ticker.removeEventListener("tick", stage.current);
        }
    }, [t('lang')]);
    return <canvas className={className} ref={anchorRef}/>
}

AdobeAnimate = React.forwardRef(AdobeAnimate);
export default AdobeAnimate
