function DownloadButton({links}) {
    const {t} = useTranslation();
    const classes = useStyles();
    const [defaultIndex, setDefaultIndex] = useState(0);
    const [downloadMenu, setDownloadMenu] = useState(null);
    const anchorRef = React.useRef(null);
    const handleClick = (event) => {
        setDownloadMenu(event.currentTarget);
    };
    const handleClose = (event) => {
        if (event.currentTarget.nodeName === 'A') {
            setDefaultIndex(parseInt(event.currentTarget.attributes.index.value));
        }
        setDownloadMenu(null);
    };
    return <ButtonGroup variant="contained" className={classes.buttonGroup}>
        <Button className={classes.button} component='a' download
                href={links[defaultIndex].src[t('lang')]}>{t('Download')}{links.length >= 1 ? ` for ${links[defaultIndex].platform}(${links[defaultIndex].name})` : null}</Button>
        {links.length > 1 ? <Button className={classes.button}
                                    ref={anchorRef}
                                    aria-owns={downloadMenu ? 'download-menu' : undefined}
                                    aria-haspopup="true"
                                    onClick={handleClick}
        >
            {links[defaultIndex].name}
            <ExpandMoreIcon fontSize="small"/>
        </Button> : null}
        {links.length > 1 ? <Popper
            open={Boolean(downloadMenu)}
            anchorEl={anchorRef.current}
            onClose={handleClose}
        >
            <div className={classes.download}>
                <ClickAwayListener onClickAway={handleClose}>
                    <MenuList
                        id="download-menu"
                        autoFocusItem={Boolean(downloadMenu)}
                    >
                        {links.map((link, index) => (
                            <MenuItem
                                component='a'
                                data-no-link="true"
                                key={index}
                                index={index}
                                onClick={handleClose}
                                download
                                href={link.src[t('lang')]}
                            >
                                {link.platform}({link.name})
                            </MenuItem>
                        ))}
                    </MenuList>
                </ClickAwayListener>
            </div>
        </Popper> : null}
    </ButtonGroup>;
}

