import React, {useEffect, useRef, useState} from "react";
import Grid from "@material-ui/core/Grid";
import {makeStyles} from "@material-ui/core";
import {useSnackbar} from "notistack";
import {useTranslation} from '../language/translater';
import axios from "axios";
import Button from "@material-ui/core/Button";
import {getOrderTradeIdLocalStorage, setOrderTradeIdLocalStorage} from "../../utils";
import CircularProgress from "@material-ui/core/CircularProgress";
import {OrderDetailsShowOnlyTable} from "../orders/ShowOnly";
import Container from "@material-ui/core/Container";

const searchParams = new URL(window.location.href).searchParams;
const name = searchParams.get('id');
const token = searchParams.get('token');
const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    fullWidth: {
        width: '100%',
    },
}));

function PaypalCheckout({tradeId, className, style}) {
    const [paying, setPaying] = useState(false);
    const [finished, setFinished] = useState(false);
    const [complete, setComplete] = useState(getOrderTradeIdLocalStorage(name, false));
    const classes = useStyles();
    const paypalRef = useRef();
    const {t} = useTranslation();
    const {enqueueSnackbar} = useSnackbar();
    const handleUpload = () => {
        axios.post('/pay/changeOrderToPending', {id: name, tradeId, lang: localStorage.getItem('lang')}, {
            headers: {'Authorization': 'Bearer ' + token}
        })//理论上不用上传tradeId，但是先上传吧
            .then((res) => {
                if (res.data.code) return enqueueSnackbar(t('tips.uploadTransactionIdFailed'), {variant: 'error'});
                setFinished(true);
                enqueueSnackbar(t('tips.checkoutSuccess'), {variant: 'success'});
            })
            .catch(e => {
                if (e.response && 401 === e.response.status) return enqueueSnackbar(t('tips.uploadTransactionIdFailedCauseAuth'), {variant: 'error'});
                enqueueSnackbar(t('tips.uploadTransactionIdFailed'), {variant: 'error'});
                console.error(e)
            })
            .finally(() => setPaying(false));
        setPaying(true);
    };
    useEffect(() => {
        window.paypal
            .Buttons({
                createOrder: (data, actions) => {
                    return Promise.resolve(tradeId)
                },
                onApprove: async (data, actions) => {
                    setPaying(true);
                    actions.order.capture()
                        .then(function (details) {
                            console.log(details);
                            setComplete(true);
                            setOrderTradeIdLocalStorage(name, true);
                            handleUpload();
                        })
                        .catch(e => {
                            enqueueSnackbar(t('tips.transactionFailed'), {variant: 'error'});
                            console.error('onApprove', e);
                        })
                        .finally(() => setPaying(false));
                },
                onError: (err) => {
                    if ("PAYMENT_ALREADY_DONE" === err.message) return enqueueSnackbar(t('tips.checkPayAlready'), {variant: 'info'});
                    enqueueSnackbar(t('tips.unknownErrorOnPay'), {variant: 'error'});
                    console.log('onError', err);
                    window.aaaaa = err;
                },
            })
            .render(paypalRef.current);
    }, []);
    return <Grid
        container direction="column" justify="center" alignItems="center" className={className || classes.root}
        style={style}
    >
        {tradeId ? <p>{t('Transaction Id') + t('：') + tradeId}</p> : null}
        {paying ? <Grid
            container justify="center" alignItems="center" wrap="nowrap"
        ><CircularProgress/><span>{t('tips.onPay')}</span></Grid> : null}
        {finished ?
            <Button fullWidth variant="contained" disabled>{t('Success')}</Button> :
            <div ref={paypalRef} className={classes.fullWidth}/>}
        {complete ? <Button variant="contained" color="primary" fullWidth
                            onClick={handleUpload}>{t('Resubmit')}</Button> : null}
        {complete ? <p>{t('tips.whatIsResubmit')}</p> : null}
    </Grid>
}

export default function OrderDetailsPaypal({data}) {
    return <Container maxWidth="sm">
        <OrderDetailsShowOnlyTable toFixed={2} data={data}/>
        <PaypalCheckout tradeId={data.tradeId}/>
    </Container>
}
