const translates = {
    'zh-CN': {
        "Change node's attributes and save.": "修改节点属性并保存",
        "The same process for date.":"date节点同理",
        "Ctrl + Mouse Left Button.": "Ctrl + 鼠标左键",
        "Make a preview.": "规则预览",
        "Save all rules.": "保存规则",
    }
};
const t = (text) => {
    const lang = localStorage.getItem('lang');
    if (translates[lang] && translates[lang][text]) return translates[lang][text];
    return text;
};

export {t}
