import {useTranslation,} from '../components/language/translater';
import React from "react";
import {makeStyles} from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import NotSupportPayment from "../components/payments/NotSupportPayment";

const config = {
    'wechat': '/images/wechat.png',
};

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
}));
function SupportQrcode(props) {
    const {t} = useTranslation();
    return <Grid
        container
        justify="center"
        alignItems="center"
        direction="column"
    >
        <img src={config[props.payment]}/>
        <div>{t('qrcodePersonal.keepPressQrcodeToPay')}</div>
    </Grid>
}

export default function Qrcode(props) {
    const searchParams = new URL(window.location.href).searchParams;
    const payment = searchParams.get('payment');
    console.log(payment)
    const classes = useStyles();
    return <Grid
        container
        direction="column"
        className={classes.root}
    >
        {config[payment]? <SupportQrcode payment={payment}/>: <NotSupportPayment/>}
    </Grid>
}
