import {Link as RouterLink} from "react-router-dom";
import Toolbar from "@material-ui/core/Toolbar";
import Avatar from "@material-ui/core/Avatar";
import Typography from "@material-ui/core/Typography";
import Link from "@material-ui/core/Link";
import LanguageSelector from "../language/selector";
import {makeStyles} from "@material-ui/core";
import AppBar from "@material-ui/core/AppBar";
import {useTranslation} from '../language/translater';

const useStyles = makeStyles((theme) => ({
    bar: {
        backgroundColor: '#fff',
        color: '#000'
    },
    title: {
        flexGrow: 1,
        '& > * + *': {
            marginLeft: theme.spacing(2),
        },
    },
}));
const links=[
    {to:'/',name:'Home'},
    {to:'/download',name:'Download'},
    {to:'/guide',name:'Guide'},
    {to:'/about',name:'About'},
];
export default function Header() {
    const classes = useStyles();
    const {t} = useTranslation();
    return <AppBar position="sticky" className={classes.bar}>
        <Toolbar className={classes.title}>
            <Avatar src="/favicon.ico" />
            <Typography className={classes.title}>
                {links.map(i=><Link key={i.name} component={RouterLink} color="inherit" to={i.to}>{t(i.name)}</Link>)}
            </Typography>
            <LanguageSelector/>
        </Toolbar>
    </AppBar>;
}
