import {useTranslation} from '../language/translater';
import Paper from "@material-ui/core/Paper/Paper";
import {BigNumber} from "bignumber.js";
import {STATUS_REVERSE} from "../../constants/status";
import React from "react";
import TableContainer from "@material-ui/core/TableContainer";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import Container from "@material-ui/core/Container";
import TableBody from "@material-ui/core/TableBody";
import {makeStyles} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
    breakWord: {
        wordBreak: 'break-all',
    }
}));

function OrderDetailsShowOnlyTable({data, toFixed}) {
    const {t} = useTranslation();
    return <>
        <p>{t('Order Id') + t('：') + data.name}</p>
        <TableContainer component={Paper}>
            <Table size="small" aria-label="a dense table">
                <TableHead>
                    <TableRow>
                        <TableCell>{t('Goods Name')}</TableCell>
                        <TableCell align="right">{t('Unit Price')}({t(data.currencyName)})</TableCell>
                        <TableCell align="right">{t('Amount')}</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {Object.entries(data.items).map(([sku, item]) => (
                        <TableRow key={sku}>
                            <TableCell component="th" scope="row">
                                {item.name}
                            </TableCell>
                            <TableCell
                                align="right">{BigNumber(item.price).multipliedBy(BigNumber(data.exchange)).toFixed(toFixed)}</TableCell>
                            <TableCell align="right">{item.amount}</TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
        <p>{t('Total')}({t(data.currencyName)}): {BigNumber(data.total).toFixed(toFixed)}</p>
        {BigNumber(data.platformFee).isGreaterThan(0) ?
            <>
                <p>{t('Platform Fee')}({t(data.currencyName)}){t('：')}{BigNumber(data.platformFee).toFixed(2)}</p>
                <p>{t('tips.platformFee')}</p>
            </>
            : null}
    </>
}

function OrderDetailsShowOnly({data}) {
    const classes = useStyles();
    const {t} = useTranslation();
    return <Container maxWidth="sm">
        <p>{t('Payment')}{t('：')}{t(`pay.${data.payment}`)}</p>
        <p>{t('Status')}{t('：')}{t(`status.${STATUS_REVERSE[data.status]}`)}</p>
        <p className={classes.breakWord}>{t('Transaction Id') + t('：') + data.tradeId}</p>
        <OrderDetailsShowOnlyTable toFixed={15} data={data}/>
        <p>{t('tips.contact')}</p>
    </Container>
}

export {OrderDetailsShowOnlyTable,OrderDetailsShowOnly}
